
.timeline-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  position: relative;
}
.timeline-title svg {
  position: absolute;
  height: 100%;
}
.timeline-title h2 {
  margin-left: 30px;
  height: 24px;
  line-height: 24px;
  color: #00B0FF;
}



