
html,
body,
#app {
  margin: 0;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  color: #212121;
}
html #loading,
  body #loading,
  #app #loading {
    background-color: #00B0FF;
    height: 100%;
}
html #content,
  body #content,
  #app #content {
    padding: 30px 0 60px 0;
}
p {
  margin: 0;
}
pre {
  font-family: inherit;
  margin: 0;
  white-space: pre-wrap;
}
h2 {
  margin: 0;
}
a {
  color: #212121;
}



