
.list-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  position: relative;
  margin-bottom: 30px;
}
.list-item svg {
    position: absolute;
    height: 100%;
}
.list-item svg ellipse {
      fill: #64DD17;
}
.list-item p {
    margin-left: 30px;
    line-height: 24px;
}



